@font-face {
  font-family: "GOT";
  font-style: normal;
  font-weight: 400;
  src: local("GOT"), local("GOT"), url("./fonts/got.ttf") format("truetype");
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

body{
  min-height: 100vh;
}

.App {
  margin: auto;
  background: blueviolet;
 min-height: 100vh;
}

header {
  font-family: GOT;
  padding: 20px 5px;
  font-weight: bold;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
  background: rgb(72, 9, 245);
}

button:focus{
  border: none;
  outline: none;
}

.add-question{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: rgb(17, 41, 252);
  color: white;
  position: fixed;
  right: 30px;
  bottom: 15px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-question img{
  width: 20px
}